.searchPage_header {
    display: flex;
    position: sticky;
    top: 0;
    background-color: white;
    z-index: 100;
    align-items: flex-start;
    padding: 30px;
    border-bottom: 1px solid lightgray;
}

.searchPage_logo {
    object-fit: contain;
    height: 50px;
    margin-right: 50px;
}

.searchPage_headerBody>.search>.search_input {
    margin-top: 0;
    width: unset;
    margin: unset;
    max-width: unset;
}

.searchPage_options {
    display: flex;
    align-items: center;
    color: gray;
}

.searchPage_options a,
.searchPage_optionsRight a {
    text-decoration: none;
    color: gray;
}

.searchPage_optionsRight,
.searchPage_optionsLeft {
    display: flex;
}

.searchPage_optionsRight {
    margin-left: 80px;
}

.searchPage_option {
    display: flex;
    align-items: center;
    margin-right: 20px;
}

.searchPage_option a {
    margin-left: 5px;
    cursor: pointer;
}

.searchPage_result {
    margin: 40px 0;
}

.searchPage_resultCount {
    color: #70757a;
    font-size: 14px;
}

.searchPage_results {
    /* max-width: 850px; */
    margin-top: 20px;
    margin-left: 60px;
    margin-bottom: 100px;
}

.searchPage_resultTitle {
    text-decoration: none;
}

.searchPage_resultTitle:hover {
    text-decoration: underline;
}

.searchPage_resultTitle>h2 {
    font-weight: 500;
}

.searchPage_resultSnippet {
    margin-top: 10px;
}

.searchPage_resultImage {
    object-fit: contain;
    height: 20px;
    width: 30px;
    margin-right: 10px;
}

html,
body {
    height: 100%;
    margin: 0
}

#viewport {
    min-height: 100%;
    position: relative;
    width: 100%
}

.content {
    padding-bottom: 35px
}

#footer {
    bottom: 0;
    font-size: 10pt;
    height: 35px;
    position: fixed;
    z-index: 104;
    width: 100%
}

#gog {
    padding: 3px 8px 0
}

td {
    line-height: .8em
}

.gac_m td {
    line-height: 17px
}

body,
td,
a,
p,
.h {
    font-family: arial, sans-serif
}

.h {
    color: #1a0dab;
    font-size: 20px
}

.q {
    color: #00c
}

em {
    font-weight: bold;
    font-style: normal
}

#lst-ib:hover {
    border-color: #a0a0a0 #b9b9b9 #b9b9b9 #b9b9b9 !important
}

#lst-ib.lst-d-f,
#lst-ib:hover.lst-d-f {
    border-color: #4d90fe !important
}

.ds {
    display: inline-block;
    margin-left: 4px
}

.ctr-p {
    margin: 0 auto;
    min-width: 980px
}

.big .ctr-p {
    min-width: 1139px;
}

body {
    background: #fff;
    color: #222
}

a {
    color: #1a0dab;
    text-decoration: none
}

a:hover,
a:active {
    text-decoration: underline
}

.fl a {
    color: #1a0dab
}

a:visited {
    color: #609
}

.sblc {
    padding-top: 5px
}

.sblc a {
    display: block;
    margin: 2px 0;
    margin-left: 13px;
    font-size: 11px
}

.lsbb {
    height: 30px;
    display: block
}

#footer a {
    color: #5f6368;
    margin: 2px 10px 0
}

#footer a:active {
    color: #dd4b39
}

.lsb {
    border: none;
    color: #000;
    cursor: pointer;
    height: 30px;
    margin: 0;
    outline: 0;
    vertical-align: top
}

.lst:focus {
    outline: none
}

body,
html {
    font-size: small
}

h1,
ol,
ul,
li {
    margin: 0;
    padding: 0
}

.hp #logocont {
    display: none
}

#body,
#footer {
    display: block
}

.gl {
    white-space: nowrap
}

.hp #tsf {
    margin: 0 auto;
    width: 833px;
}

#tsf {
    width: 833px
}

.big #tsf,
.hp .big #tsf {
    width: 1139px;
}

#fll {
    float: right;
    text-align: right;
    width: 100%
}

#ftby {
    padding-left: 0
}

#ftby>div,
#fll>div,
#footer a {
