    #ftby {
        margin: 0 44px
    }
}

.logo-subtext {
    color: #4285f4;
    font: 16px/16px roboto-regular, arial, sans-serif;
    left: 215px;
    position: relative;
    top: 76px;
    white-space: nowrap
}

[dir='ltr'],
[dir='rtl'] {
    unicode-bidi: -webkit-isolate;
    unicode-bidi: isolate
}

bdo[dir='ltr'],
bdo[dir='rtl'] {
    unicode-bidi: bidi-override;
    unicode-bidi: -webkit-isolate-override;
    unicode-bidi: isolate-override
}

